import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/layout/Layout'
Vue.use(Router)

export const constantRouterMap = [
  {
    path: '',
    component: Layout,
    redirect: 'dashboard',
    children: [
      {
        path: 'dashboard',
        component: () => import('@/views/dashboard/index'),
        name: 'dashboard',
        meta: { title: '首页', icon: 'dashboard', noCache: true, roles: ['无'] }
      }
    ]
  },
  {
    path: '/login',
    meta: {roles: ['无']},
    component: () => import('@/views/login/index'),
    hidden: true
  },
  {
    path: '/auth-redirect',
    meta: {roles: ['无']},
    component: () => import('@/views/login/authredirect'),
    hidden: true
  },
  {
    path: '/404',
    meta: {roles: ['无']},
    component: () => import('@/views/errorPage/404'),
    hidden: true
  },
  {
    path: '/401',
    meta: {roles: ['无']},
    component: () => import('@/views/errorPage/401'),
    hidden: true
  }]
export const asyncRouterMap = [
  {
    path: '/sys',
    component: Layout,
    name: 'sysManage',
    meta: {
      roles: ['公司管理人员'],
      title: '系统管理',
      icon: 'chart'
    },
    children: [
      {
        path: 'admin',
        component: () => import('@/views/sys/admin'),
        name: 'admin',
        meta: {roles: ['公司管理人员'], title: '管理员', noCache: true}
      },
      {
        path: 'role',
        component: () => import('@/views/sys/role'),
        name: 'role',
        meta: {roles: ['公司管理人员'], title: '角色管理', noCache: true}
      }
    ]
  },
  {
    path: '/info',
    component: Layout,
    redirect: 'noredirect',
    name: 'userManager',
    meta: {
      roles: ['数据录入人员'],
      title: '信息录入',
      icon: 'chart'
    },
    children: [
      {
        path: 'user',
        component: import('@/views/info/user'),
        name: 'user',
        meta: {title: '用户信息', roles: ['数据录入人员']}
      },
      {
        path: 'plot',
        component: import('@/views/info/plot'),
        name: 'plot',
        meta: {title: '小区信息', roles: ['数据录入人员']}
      },
      {
        path: 'address',
        component: import('@/views/info/address'),
        name: 'address',
        meta: {title: '地址信息', roles: ['数据录入人员']}
      },
      {
        path: 'device',
        component: import('@/views/info/device'),
        name: 'device',
        meta: {title: '机型信息', roles: ['数据录入人员']}
      },
      {
        path: 'room',
        component: import('@/views/info/room'),
        name: 'room',
        meta: {title: '机房信息', roles: ['数据录入人员']}
      }
    ]
  },
  {
    path: '/fault',
    component: Layout,
    redirect: 'noredirect',
    alwaysShow: true,
    name: 'fault',
    meta: {
      title: '运维人员',
      icon: 'chart',
      roles: ['运维人员']
    },
    children: [
      {
        path: 'faultRecords',
        component: () => import('@/views/fault/faultRecords'),
        name: 'faultRecords',
        meta: {
          roles: ['运维人员'],
          title: '故障记录',
          noCache: true
        }
      }, {
        path: 'realTimeWater',
        component: () => import('@/views/fault/realTimeWater'),
        name: 'realTimeWater',
        meta: {
          roles: ['运维人员'],
          title: '实时需水量',
          noCache: true
        }
      }, {
        path: 'plotFactor',
        component: () => import('@/views/fault/plotFactor'),
        name: 'plotFactor',
        meta: {
          roles: ['运维人员'],
          title: '小区系数',
          noCache: true
        }
      }, {
        path: 'factorUpdateRecord',
        component: () => import('@/views/fault/factorUpdateRecord'),
        name: 'factorUpdateRecord',
        meta: {
          roles: ['运维人员'],
          title: '系数修改记录',
          noCache: true
        }
      }]
  },
  {
    path: '/faultLeader',
    component: Layout,
    redirect: 'noredirect',
    alwaysShow: true,
    name: 'faultLeader',
    meta: {
      roles: ['故障领导'],
      title: '故障领导',
      icon: 'chart'
    },
    children: [{
      path: 'faultRecords',
      component: () => import('@/views/fault/faultRecords'),
      name: 'faultRecordsLeader',
      meta: {
        roles: ['故障领导'],
        title: '故障记录',
        noCache: true
      }
    }, {
      path: 'deviceMonitor',
      component: () => import('@/views/fault/deviceMonitor'),
      name: 'deviceMonitor',
      meta: {
        roles: ['故障领导'],
        title: '设备分组',
        noCache: true
      }
    }]
  }
]
export default new Router({
  scrollBehavior: () => ({y: 0}),
  routes: constantRouterMap
})



